import React, { Component } from 'react';
import 'froala-editor/js/froala_editor.pkgd.min.js';
import 'froala-editor/css/froala_style.min.css';
import 'froala-editor/css/froala_editor.pkgd.min.css';
import 'font-awesome/css/font-awesome.css';
import { Modal, Button,  Form, Icon, Input, Checkbox,Table, Divider, Tag,Tabs,Select  } from 'antd';

import FroalaEditor from 'react-froala-wysiwyg';
import $ from "jquery";
window.$ = $;
const { Column, ColumnGroup } = Table;

const FormItem = Form.Item;
const TabPane = Tabs.TabPane;
const Option = Select.Option;


class Faq extends Component {
  constructor(props){
    super(props);
      this.state = {
        addFaqVisible:false,
        addCategoryVisible:false,
        editCategoryVisible:false,
        viewCategoryVisible:false,
        addCategoryName:null,
        addCategoryDescription:null,
        editCategoryName:null,
        editCategoryId:null,
        editCategoryDescription:null,
        getFaqData: [],
        getCategoryData:[],
        addFaqQuestion:null,
        addFaqAnswer:'Type Something',
        addFaqCategory:null,
        selectCategoryDefault:null,

    }
  }
  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Constructor
  */

  //CRUD
  getFaq = () => {
    const data = JSON.stringify({
      "query":"query( $limit:limit,$sort:sort,$search:search ){ getQuestionFAQ( limit:$limit,sort:$sort,search:$search ) { sortableFields,searchableFields,page { items,pageNo,totalPages },data { id,question,answer,created },success,message { path,message } } }",
       "variables":{
         "limit":{
           "to":5,
           "page":1
         },
         "sort":{
           "field":"",
           "by":""
         },
         "search":{
             "by":"",
             "text":""
          }
       }
    })
   fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
			const response = await res.json()
      let Result = response.getQuestionFAQ.data;
      for (let item in Result){
        Result[item].key=item;
        Result[item].id = item;
      }
      if(response.getQuestionFAQ.success){
        this.setState({
          getFaqData:Result
        })
        // console.log("getFaqData: ", this.state.getFaqData)
      }else{
        return false
      }
		})
  }

  addFaq = async () => {
    const data = JSON.stringify({
      "query":"mutation($input:faq_inptFields!){ createQuestionFAQ( input:$input ) { success,message { path,message } } }",
        "variables":{
            "input":{
                "question":this.state.addFaqQuestion,
                "answer":this.state.addFaqAnswer,
                "category":this.state.addFaqCategory
            }
        }
    })
    return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log("addFaq: ",response)
      if(response.createQuestionFAQ.success){
        return true
      }else{
        return false
      }
    })
  }



  //SUCCESS MODAL
  success = () => {
  Modal.success({
    title: 'Success',
    maskClosable:'false'
    });
  }
  //OPEN MODAL
  openAddFaqModal = async (e) => {
    this.setState({
      addFaqVisible: true,
    });

    this.removeLicenseByDarwin()

  }
  //MODAL
  addFaqModal = async  () => {

    if(await this.addFaq()){
      this.success()

      this.setState({
        addFaqVisible: false,
      });
      this.getFaq()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }
  //CLOSE MODAL
  closeAddFaqModal = () => {
    console.log(document.getElementsByClassName('froala'))

    this.setState({
      addFaqVisible: false,
    });
  }

  handleChange = (e) => {
    console.log('selected value',e)
    this.setState({
      addFaqCategory:e
    })
  }

  handleModelChange = (e) => {
    this.setState({addFaqAnswer: e});
    // console.log('hehe: ',this.state.addFaqAnswer)

    // $(e).find('.fr-wrapper').on('change',(event) => {
    //   alert('froalachange');
    // });
  }


  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Methods
  */

  /*
    __-----_________________{]__________________________________________________
   {&&&&&&&#%%&#%&%&%&%&%#%&|]__________________________________________________\ CATEGORY
                            {]
  */

  //CRUD
  getCategory = () => {
    const data = JSON.stringify({
      "query":"query{ getCategoryFAQ { data{id,name,description,created},success,message { path,message } } }"
    })
   fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      // console.log(response)
      let Result = response.getCategoryFAQ.data;
      for (let item in Result){
        Result[item].key=item;
        // Result[item].id = item;
      }
      if(response.getCategoryFAQ.success){
        this.setState({
          getCategoryData:Result
        })
        console.log("getCategory: ", this.state.getCategoryData)
        this.setAddFaqSelectDefault()
      }else{
        return false
      }
    })
  }

  addCategory = async () => {
    const data = JSON.stringify({
      "query":"mutation($input:categoryName!){ createCategoryFAQ( input:$input ) { success,message { path,message } } }",
      "variables":{
          "input":{
            "name":this.state.addCategoryName,
            "description":this.state.addCategoryDescription
        }
      }
    })
   return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log(response)
      if(response.createCategoryFAQ.success){
        return true
      }else{
        return false
      }
    })
  }

  editCategory = async () => {
    const data = JSON.stringify({
        "query":"mutation($input:updateCategoryName!){ updateCategoryFAQ( input:$input ) { success,message { path,message } } }",
        "variables":{
          "input":{
            "id":this.state.editCategoryId,
            "name":this.state.editCategoryName,
            "description":this.state.editCategoryDescription
        }
      }
    })
   return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log(response)
      if(response.updateCategoryFAQ.success){
        return true
      }else{
        return false
      }
    })
  }

  deleteCategory = async (e) => {
    let deleteCategoryId = []
    deleteCategoryId.push(e)
    const data = JSON.stringify({
      "query":"mutation($input:[String]!){ deleteCategoryFAQ( input:$input ) { success,message { path,message } } }",
      "variables":{
          "input":deleteCategoryId
      }
    })
   return await fetch("https://api.8uilder.com",{
      method:'POST',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("session")
      },
      body:data
    }).then(async res =>{
      const response = await res.json()
      console.log(response)
      if(response.deleteCategoryFAQ.success){
        return true
      }else{
        return false
      }
    })
  }


  //SUCCESS
  success = () => {
  Modal.success({
    title: 'Success',
    maskClosable:'false'
    });
  }

  //OPEN MODAL
  openAddCategoryModal = () => {
    this.setState({
      addCategoryVisible:true
    })
  }

  openViewCategoryModal = () => {
    this.setState({
      viewCategoryVisible:true
    })
  }

  openEditCategoryModal = (e) => {
    this.setState({
      editCategoryVisible:true,
      editCategoryId: e.id,
      editCategoryName: e.name,
      editCategoryDescription: e.description,
    })
  }

  //CLOSE MODAL
  closeAddCategoryModal = () => {
    this.setState({
      addCategoryVisible:false
    })
  }

  closeViewCategoryModal = () => {
    this.setState({
      viewCategoryVisible:false
    })
  }

  closeEditCategoryModal = () => {
    this.setState({
      editCategoryVisible:false
    })
  }

  //MODAL
  addCategoryModal = async () => {
    if(await this.addCategory()){
      this.success()
      this.setState({
        addCategoryVisible:false
      })
      let addCategoryInput = await document.getElementsByClassName('addCategoryInput')
      for(var i = 0;i < addCategoryInput.length;i++){
        addCategoryInput[i].children[1].value = ""
      }
      this.getCategory()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }

  editCategoryModal = async () => {
    if(await this.editCategory()){
      this.success()
      this.setState({
        editCategoryVisible:false
      })
      this.getCategory()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }

  deleteCategoryModal = async (e) => {
    if(await this.deleteCategory(e.id)){
      this.success()
      this.getCategory()
    }else{
      Modal.error({
        title: 'This is an error message',
        maskClosable:'false'
      });
    }
  }

  //RANDOM

  setAddFaqSelectDefault = () => {
    console.log(this.state.getCategoryData)
    if(this.state.getCategoryData.length > 0){
      this.setState({
        selectCategoryDefault:this.state.getCategoryData[0].name,
        addFaqCategory:this.state.getCategoryData[0].name
      })

    }else{
      this.setState({
        selectCategoryDefault:"Empty"
      })
    }
  }

  removeLicenseByDarwin = async () => {
    // const parentDiv = await document.getElementsByClassName('fr-wrapper')
    // const firstChild = parentDiv[0].firstElementChild
    //  console.log(firstChild)
    //  parentDiv[0].removeChild(firstChild)
    $(".fr-tooltip").hover(function(){
      alert("asdas")
      $(this).find("a").remove();
    });
  }


  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Methods
  */


  //ONCHANGE
  onChange = (e) =>{
    this.setState({[e.target.name]:e.target.value})
  }
  callback = (key) => {
  }

  componentWillMount(){
    this.getFaq();
    this.getCategory();
    $(".fr-tooltip").on('click',function(){
      alert("asdas")
    });
    $(".fr-tooltip").on('change',function(){
      alert("asdasaaa")
    });
  }
  componentDidMount(){
    $(".fr-tooltip").on('change',function(){
      alert("asabbbb")
    });
    $(".fr-tooltip").on('click',function(){
      alert("abbbasdas")
    });
  }


  /*
             |||||||
            ( O   O )
     ____oOO___(_)___OOo____
    (_______________________) Life Cycle
  */


  render() {

    return (
      <div className="faqAndCategory">

        <Tabs defaultActiveKey="1" onChange={(e) => this.callback =(e)}>
          <TabPane tab="FAQ" key="1">
            <div className="roleComponent">
              <h1>FAQ</h1>
              <div className="row" style={{marginBottom:"10px"}}>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3">

                </div>

                <div className="col-sm-3">
                  <button className="btn btn-sm btn-primary" style={{float:"right"}} onClick={this.openAddFaqModal}>Create FAQ</button>

                </div>
              </div>
              <Table dataSource={this.state.getFaqData}>
                <Column title="Date Created"dataIndex="created"/>
                <Column title="Questions"dataIndex="question"/>
                <Column title="Answer" dataIndex="answer"/>
                <Column title="Action" key="action"
                  render={(text, record) => (
                    <span>
                      <a href="#" onClick={() => this.openeditRolesModal(record)}>Edit</a>
                      <Divider type="vertical" />
                      <a href="#" onClick={() => this.deleteRolesModal(record)}>Delete</a>
                      <Divider type="vertical" />
                      <a href="#"  onClick={() => this.openSetRolesModal(record)}>Set</a>
                    </span>
                   )}
                />
              </Table>
              <div className="Modal">
                <Modal
                  title="Create FAQ"
                  width="1000px"
                  visible={this.state.addFaqVisible}
                  onOk={this.addFaqModal}
                  onCancel={this.closeAddFaqModal}
                  maskClosable={false}>
                  <div>
                    <Form className="login-form">
                      <FormItem>
                        <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Faq Question"  onChange={this.onChange} name="addFaqQuestion" />
                      </FormItem>
                      <div className="select">
                        <Select defaultValue={this.state.selectCategoryDefault} style={{ width: '100%' }} onChange={this.handleChange}>
                          {this.state.getCategoryData.map((items,index) =>
                          <Option key={items.name+index} value={items.name}>{items.name}</Option>
                          )}
                        </Select>
                      </div>
                      <div className="froala">
                          <div style={{width:"800",margin:"20px auto"}}>
                            <FroalaEditor
                              tag='textarea'
                              config={this.config}
                              model={this.state.addFaqAnswer}
                              onModelChange={this.handleModelChange}/>
                          </div>
                      </div>
                   </Form>
                  </div>
                </Modal>
              </div>
            </div>
          </TabPane>

          {/* TAB CATEGORY  */}

          <TabPane tab="Category" key="2">
            <div className="roleComponent">
              <h1>Category</h1>
              <div className="row" style={{marginBottom:"10px"}}>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3"></div>
                <div className="col-sm-3">
                  <button className="btn btn-sm btn-primary" style={{float:"right"}} onClick={this.openAddCategoryModal}>Create Category</button>
                </div>
              </div>
              <Table dataSource={this.state.getCategoryData}>
                <Column title="Date Created"dataIndex="created"/>
                <Column title="Name"dataIndex="name"/>
                <Column title="Description" dataIndex="description"
                />
                <Column title="Action" key="action"
                  render={(text, record) => (
                    <span>
                      <a href="#" onClick={() => this.openEditCategoryModal(record)}>Edit</a>
                      <Divider type="vertical" />
                      <a href="#" onClick={() => this.deleteCategoryModal(record)}>Delete</a>
                      <Divider type="vertical" />
                      <a href="#"  onClick={() => this.openViewCategoryModal(record)}>View</a>
                    </span>
                   )}
                />
              </Table>
              <div className="Modal">
                <Modal
                  title="Add Category"
                  visible={this.state.addCategoryVisible}
                  onOk={this.addCategoryModal}
                  onCancel={this.closeAddCategoryModal}
                  maskClosable={false}>
                  <div>
                    <Form className="add-form">
                      <FormItem>
                        <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Category Name" className="addCategoryInput"  onChange={this.onChange} name="addCategoryName" />
                      </FormItem>
                      <FormItem>
                        <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Category Description" className="addCategoryInput" onChange={this.onChange} name="addCategoryDescription" />
                      </FormItem>
                   </Form>
                  </div>
                </Modal>
                <Modal
                  title="Edit Category"
                  visible={this.state.editCategoryVisible}
                  onOk={this.editCategoryModal}
                  onCancel={this.closeEditCategoryModal}
                  maskClosable={false}>
                  <div>
                    <Form className="edit-form">
                      <FormItem>
                        <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Category Name" className="editCategoryInput"  onChange={this.onChange} name="editCategoryName" value={this.state.editCategoryName} />
                      </FormItem>
                      <FormItem>
                        <Input prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Category Description" className="editCategoryInput" onChange={this.onChange} name="editCategoryDescription" value={this.state.editCategoryDescription} />
                      </FormItem>
                   </Form>
                  </div>
                </Modal>
                <Modal
                  title="View Category"
                  visible={this.state.viewCategoryVisible}
                  onOk={this.closeViewCategoryModal}
                  onCancel={this.closeViewCategoryModal}
                  maskClosable={true}>
                  <div>
                    <h1>VIEW</h1>
                  </div>
                </Modal>
              </div>
            </div>
          </TabPane>
        </Tabs>,
      </div>
    );
  }
}

export default Faq;
